@import "../../base/variables.scss";

.like-button {
    display: inline-flex;
    width: 40px;
    height: 20px;

    font-size: 10px;
    line-height: 1;
    color: $color-ds25;

    background: transparent;
    border: 1px solid $color-ds25;
    border-radius: 10px;
    cursor: pointer;

    transition: all .1s;

    &--clicked {
        color: $color-purple;
        border-color: $color-purple;
    }
}

.like-button__icon {
    margin-right: 4px;
    font-size: inherit;
}

.like-button__count {
    margin: 0 auto;
}